.fonts {
  &-block {
    width: 100%;
    max-width: 436px;
    min-width: 436px;
    width: 30.2%;
    margin-right: 158px;
    &__item {
      display: flex;
      column-gap: 30px;
      margin-bottom: 51px;
      &:last-child {
        margin-bottom: 0;
      }
      &:nth-child(3) {
        margin-bottom: 49px;
      }
    }
    &__sample {
      width: 100%;
      max-width: 37px;
      display: flex;
      justify-content: flex-end;
      // color: base.$dark25;
      &_fs24 {
        font-size: 24px;
        line-height: 29.26px;
      }
      &_fs19 {
        font-size: 19px;
      }
      &_fs12 {
        font-size: 12px;
        line-height: 15.63px;
      }
      &_fs14 {
        font-size: 14px;
        line-height: 23px;
      }
